import users from "../models/user";
import loans from "../models/loans";
import repayments from "../models/repayments";
import validationResult from "express-validator/check";
import moment from "moment";

class postsController {
    
//get function
  static getloans(req, res) {
            return res.status(200).json({
              status:200,  
              message: "List of all loans",
              loan: loans
            });
  }

  static getnotrepaid(req, res) {
    const currentstatus="approved";
    const currentrepaid=false;
    const checknotrepaid = loans.find(oneloans => oneloans.status == currentstatus && oneloans.repaid == currentrepaid);
    if(checknotrepaid){
    return res.status(200).json({
      status:200,  
      message: "List of all loans that are not fully repaid",
      loan: checknotrepaid
    });
  }
  else{
    return res.status(400).json({
      status:404,  
      error: " can't find any loan that is not fully repaid",
      
    });

  }
}

static getrepaid(req, res) {
  const currentstatus="approved";
  const currentrepaid=true;
  const checkrepaid = loans.find(oneloans => oneloans.status == currentstatus && oneloans.repaid == currentrepaid);
  if(checkrepaid){
  return res.status(200).json({
    status:200,  
    message: "List of all loans that are  fully repaid",
    loan: checkrepaid
  });
}
else{
  return res.status(400).json({
    status:404,  
    error: " can't find any loan that is  fully repaid",
    
  });

}
}

  static getrepayments(req, res) {
    return res.status(200).json({
      status:200,  
      message: "List of all repayments",
      candidate: repayments
    });

  }
    

//create function
static createloans(req, res) {
    const id = parseInt(loans.length) + 1;
    const {firstname,lastname,email,tenor,amount} = req.body;
    
  const checkloan = loans.find(oneloans => oneloans.email == req.body.email);
  if (checkloan) {
    return res.status(400).json({
      
      message: "loan already exist",  
    });
  } else {
    const insertloan = {
      id,
      createdOn:moment.utc().format(),
      firstname,
      lastname, 
      email,
      status:"pending",
      repaid:false,
      tenor,
      amount,
      paymentInstallment:(parseFloat(req.body.amount)+parseFloat(req.body.amount*5/100))/parseFloat(req.body.tenor),
      balance:parseFloat((parseFloat(req.body.amount)+parseFloat(req.body.amount*5/100))/parseFloat(req.body.tenor)*tenor),
      interest:req.body.amount*5/100,
      
    };
    loans.push(insertloan);
    return res.status(200).json({ 
      status:200, 
      message: "loan created successfully",
      loan:insertloan
    });
  }
  }

  static createrepayments(req, res) {
    const id = parseInt(repayments.length) + 1;
    const { loanid } = req.params;
    const {paidamount} = req.body;
    
    const getloan = loans.find(updateloan => updateloan.id == loanid);
  if (getloan) {
    const insertrepayment = {
      id,
      createdOn:moment.utc().format(),
      loanId:getloan.id,
      amount:getloan.amount,
      monthlyInstallment:getloan.paymentInstallment,
      paidamount,
      balance:parseFloat(getloan.balance)-parseFloat(req.body.paidamount),
      
    };
    repayments.push(insertrepayment);
    return res.status(200).json({ 
      status:200, 
      message: "payment done successfully",
      loan:insertrepayment
    });
    
    
  } else {
    return res.status(400).json({
      
      message: "that loan doesn't exist",  
    });
  }
  }
  
  //update function (patch)
static verifyloan(req, res) {
  const { id } = req.params;
  
  const getloan = loans.find(updateloan => updateloan.id == id);
  if (getloan) {
    (getloan.status = req.body.status);
    return res.status(201).json({
      message: "loan is approved succesfully",
      updatePost: getloan
    });
  } else {
    res.status(400).json({
      error: "can't find that loan with that id"
    });
  }
}
  

////////////////////////////////////////////////////////////////////

///////////////////////////////////////////////////////////////
//get user by id
static getOneuser(req, res) {
  const { id } = req.params;
  const user = users.find(oneuser => oneuser.id == id);
  if (user) {
    return res.status(200).json({
      message: "one user found",
      onePost: user
    });
  } else {
    res.status(400).json({
      error: "no user found with that id"
    });
  }
}

//get loan by id
static getOneloan(req, res) {
  const { id } = req.params;
  const findloan = loans.find(oneloans => oneloans.id == id);
  if (findloan) {
    return res.status(200).json({
      message: "one loan found",
      loan: findloan
    });
  } else {
    res.status(400).json({
      error: "no loan found with that id"
    });
  }
}
// get payment by id
static getOnepayment(req, res) {
  const { id } = req.params;
  const getpayment = repayments.find(onepayment => onepayment.id == id);
  if (getpayment) {
    return res.status(200).json({
      message: "payment found",
      payment: getpayment
    });
  } else {
    res.status(400).json({
      error: "no payment found with that id"
    });
  }
}





}







export default postsController;